































import Vue from 'vue'
import {Component, Prop, Watch} from 'vue-property-decorator'

@Component
export default class GnbHeader extends Vue {
    currentPath: String = ''

    created() {
        this.currentPath = this.$route.path
    }

    @Watch('$route')
    onRouteChange(): void {
        this.currentPath = this.$route.path
    }
}
